import { Link } from "react-router-dom";

const Welcome = () => {
  return (
    <>
      <div className="p-5 mb-4 bg-light rounded-3">
        <div className="container-fluid py-5">
          <h1 className="display-5 fw-bold">Resume Builder</h1>
          <p className="col-md-8 fs-4">
            Resume builder is an application which will help you to easily
            create resume in different formats and according to your choice. You
            just need to enter your information in the form and submit.
          </p>
          <Link to="/resumeform">
            <button className="btn btn-primary btn-lg" type="button">
              Let's Start
            </button>
          </Link>
        </div>
      </div>
    </>
  );
};
export default Welcome;
