import { useContext } from "react";
import { FormContext } from "../../formContext";
import classes from "./template1.module.css";
import mail from "../../images/mail.png";
import mobile from "../../images/mobile.png";
import house from "../../images/house.png";
const Template1 = () => {
  const { formData } = useContext(FormContext);
  const data = formData;
  let name = data.firstname + " " + data.middlename + " " + data.lastname;
  name = name.charAt(0).toUpperCase() + name.slice(1);
  function checkvalueempty(value) {
    if (value === "") {
      return false;
    } else {
      return true;
    }
  }
  return (
    <div className="container">
      <div className={classes.resume}>
        <div className="row">
          <div className="col-md-3">
            <span className={classes.name}>{name}</span>
            <span className={classes.name2}></span>
            <br />
            <br />
            <h4>Personal Details</h4>
            <hr />
            <div className="row">
              <div className="col-md-2">
                <img src={mail} alt="mail" className={classes.mail} />
              </div>
              <div className="col-md-10">
                <a href={data.email}>{data.email}</a>
              </div>
            </div>
            {/* <hr className={classes.sidehr} /> */}
            <br />
            <div className="row">
              <div className="col-md-2">
                <img src={mobile} alt="Mobile" className={classes.mail} />
              </div>
              <div className="col-md-10">{data.Mobile}</div>
            </div>
            <br />
            <div className="row">
              <div className="col-md-2">
                <img src={house} alt="Mobile" className={classes.mail} />
              </div>
              <div className="col-md-10">{data.currentlocation}</div>
            </div>
            <br />
            <br />
            {checkvalueempty(data.skills[0].name) && (
              <>
                <h4>Skills</h4>
                <hr />
                <ul>
                  {data.skills.map((item) => {
                    return (
                      <li key={item.id}>
                        <h6>{item.name}</h6>
                      </li>
                    );
                  })}
                </ul>
              </>
            )}
            <br />
            <br />
            {checkvalueempty(data.personalLink[0].title) && (
              <>
                <h4>Personal Profiles</h4>
                <hr />
                {data.personalLink.map((item) => {
                  return (
                    <div className="row" key={item.id}>
                      <div className="col">{item.title}</div>
                      <div className="col">
                        <a href={item.link}>{item.link}</a>
                      </div>
                    </div>
                  );
                })}
              </>
            )}
            <span className={classes.bottomname}></span>
          </div>
          <div className="col-md-9">
            <h3>Summary</h3>
            <hr />
            {data.summary}
            <br />
            <br />
            {checkvalueempty(data.experiences[0].designation) && (
              <>
                <h3>Work Experience</h3>
                <hr />
                {data.experiences.map((item) => {
                  return (
                    <>
                      <div className="row">
                        <div className="col-md-9">{item.designation}</div>
                        <div className="col-md-3">
                          {item.durationfrom} To {item.durationto}
                        </div>
                      </div>
                      {item.companyname}
                      <br />
                      {item.role}
                    </>
                  );
                })}
              </>
            )}
          </div>
        </div>
        <br />
        <br />
      </div>
    </div>
  );
};
export default Template1;
