import { usePDF } from "react-to-pdf";
import Template1 from "./ResumeTemplates/template1";
import classes from "./resumeDemo.module.css"
const ResumeDemo = () => {
  const { toPDF, targetRef } = usePDF({
    filename: "Resume-builder-Resume.pdf",
  });
  return (
    <>
      <div ref={targetRef}>
        <Template1></Template1>
      </div>
<br /><br /><br />
      <div className={"container "+classes.download}>
        <button onClick={toPDF} className="btn btn-primary ">
          Download PDF
        </button>
      </div>
    </>
  );
};
export default ResumeDemo;
