import { useNavigate } from "react-router-dom";

import classes from "./resumeForm.module.css";
import { useContext, useState } from "react";
import { FormContext } from "./formContext";

const ResumeForm = () => {
  const [data, setdata] = useState({
    firstname: "",
    middlename: "",
    lastname: "",
    Mobile: "",
    email: "",
    dateofbirth: "",
    currentlocation: "",
    experiences: [
      {
        id: 0,
        designation: "",
        role: "",
        companyname: "",
        durationfrom: "",
        durationto: "",
      },
    ],
    skills: [
      {
        id: 0,
        name: "",
      },
    ],
    certificate: [
      {
        id: 0,
        title: "",
        link: "",
      },
    ],
    summary: "",
    personalLink: [
      {
        id: 0,
        title: "",
        link: "",
      },
    ],
  });
  const [fname, setfname] = useState(false);
  const [lname, setlname] = useState(false);
  const navigate = useNavigate();
  const { setFormData } = useContext(FormContext);
  const [expflag, setexpflag] = useState(false);
  const [skiflag, setskiflag] = useState(false);
  const [certiflag, setcertiflag] = useState(false);
  const [persoflag, setpersoflag] = useState(false);
  const [mob, setmob] = useState(false);
  const [emai, setemai] = useState(false);
  const [addr, setaddr] = useState(false);
  const [prosum, setprosum] = useState(false);
  function handlesubmit(e) {
    e.preventDefault();
    if (data.firstname === "") {
      window.scrollTo({ top: 0, behavior: "smooth" });
      setfname(true);
    } else if (data.lastname === "") {
      window.scrollTo({ top: 0, behavior: "smooth" });
      setlname(true);
    } else if (data.Mobile === "") {
      window.scrollTo({ top: 0, behavior: "smooth" });
      setmob(true);
    } else if (data.email === "") {
      window.scrollTo({ top: 0, behavior: "smooth" });
      setemai(true);
    }else if (data.currentlocation === "") {
      window.scrollTo({ top: 0, behavior: "smooth" });
      setaddr(true);
    }else if (data.summary === "") {
      window.scrollTo({ top: 0, behavior: "smooth" });
      setprosum(true);
    } else {
      setFormData(data);
      navigate("/generatedResume");
    }
  }
  function handleinput(event) {
    if (event.target.name === "firstname") {
      setfname(false);
    }
    if (event.target.name === "lastname") {
      setlname(false);
    }
    if (event.target.name === "Mobile") {
      setmob(false);
    }
    if (event.target.name === "email") {
      setemai(false);
    }
    if (event.target.name === "currentlocation") {
      setaddr(false);
    }
    if (event.target.name === "summary") {
      setprosum(false);
    }
    setdata({
      ...data,
      [event.target.name]: event.target.value,
    });
  }
  function handleexperience(index, event) {
    const newexperiences = [...data.experiences];
    newexperiences[index][event.target.name] = event.target.value;
    setdata({ ...data, experiences: newexperiences });
  }
  function handleskills(index, event) {
    const newskills = [...data.skills];
    newskills[index][event.target.name] = event.target.value;
    setdata({
      ...data,
      skills: newskills,
    });
  }
  function handlecerti(index, event) {
    const newcerti = [...data.certificate];
    newcerti[index][event.target.name] = event.target.value;
    setdata({
      ...data,
      certificate: newcerti,
    });
  }
  function handleprofile(index, event) {
    const newperson = [...data.personalLink];
    newperson[index][event.target.name] = event.target.value;
    setdata({
      ...data,
      personalLink: newperson,
    });
  }

  function addExperience() {
    let length = data.experiences.length;
    if (length === 3) {
      setexpflag(true);
    } else {
      setdata((prevData) => ({
        ...prevData,
        experiences: [
          ...prevData.experiences,
          {
            id: length,
            designation: "",
            role: "",
            companyname: "",
            durationfrom: "",
            durationto: "",
          },
        ],
      }));
    }
  }
  function addskills() {
    let length = data.skills.length;
    if (length === 10) {
      setskiflag(true);
    } else {
      setdata((prevData) => ({
        ...prevData,
        skills: [
          ...prevData.skills,
          {
            id: length,
            name: "",
          },
        ],
      }));
    }
  }

  function addcerti() {
    let length = data.certificate.length;
    if (length === 7) {
      setcertiflag(true);
    } else {
      setdata((prevData) => ({
        ...prevData,
        certificate: [
          ...prevData.certificate,
          {
            id: length,
            title: "",
            link: "",
          },
        ],
      }));
    }
  }

  function addperson() {
    let length = data.personalLink.length;
    if (length === 7) {
      setpersoflag(true);
    } else {
      setdata((prevData) => ({
        ...prevData,
        personalLink: [
          ...prevData.personalLink,
          {
            id: length,
            title: "",
            link: "",
          },
        ],
      }));
    }
  }
  return (
    <div className="container">
      <span className={classes.centrized}>
        <h3>Enter Your Details</h3>
      </span>
      <br />
      <form>
        <div className="row">
          <div className="col-md-4">
            <label htmlFor="firstname">
              <h5>
                First Name
                <span className="text-danger">*</span>
              </h5>
            </label>
            <input
              type="text"
              className="form-control"
              id="firstname"
              name="firstname"
              onChange={handleinput}
              required
            />
            {fname && (
              <span className="text-danger">First name is mandatory</span>
            )}
          </div>
          <div className="col-md-4">
            <label htmlFor="middlename">
              <h5>Middle Name</h5>
            </label>
            <input
              type="text"
              className="form-control"
              id="middlename"
              name="middlename"
              onChange={handleinput}
            />
          </div>
          <div className="col-md-4">
            <label htmlFor="lastname">
              <h5>
                Last Name
                <span className="text-danger">*</span>
              </h5>
            </label>
            <input
              type="text"
              className="form-control"
              id="lastname"
              name="lastname"
              onChange={handleinput}
            />
            {lname && (
              <span className="text-danger">Last name is mandatory</span>
            )}
          </div>
        </div>
        <br />
        <div className="row">
          <div className="col-md-4">
            <label htmlFor="Mobile">
              <h5>
                Mobile
                <span className="text-danger">*</span>
              </h5>
            </label>
            <input
              type="text"
              className="form-control"
              id="Mobile"
              name="Mobile"
              onChange={handleinput}
            />
            {mob && (
              <span className="text-danger">Mobile number is mandatory</span>
            )}
          </div>
          <div className="col-md-4">
            <label htmlFor="email">
              <h5>
                E-Mail
                <span className="text-danger">*</span>
              </h5>
            </label>
            <input
              type="email"
              className="form-control"
              id="email"
              name="email"
              aria-describedby="emailHelp"
              onChange={handleinput}
            />
            {emai && (
              <span className="text-danger">Contacting Email is mandatory</span>
            )}
          </div>
          <div className="col-md-4">
            <label htmlFor="dateofbirth">
              <h5>Date Of Birth</h5>
            </label>
            <input
              type="date"
              className="form-control"
              id="dateofbirth"
              name="dateofbirth"
              onChange={handleinput}
            />
          </div>
        </div>
        <br />
        <div className="row">
          <div className="col-md-4">
            <label htmlFor="currentlocation">
              <h5>
                Enter Address
                <span className="text-danger">*</span>
              </h5>
            </label>
            <input
              type="text"
              className="form-control"
              id="currentlocation"
              name="currentlocation"
              onChange={handleinput}
            />
             {addr && (
              <span className="text-danger">Address is mandatory</span>
            )}
          </div>
        </div>
        <br />
        <div className="row">
          <div className="col">
            <label htmlFor="summary">
              <h5>
                Profile Summary
                <span className="text-danger">*</span>
              </h5>
            </label>
            <textarea
              type="text"
              className="form-control"
              id="summary"
              name="summary"
              onChange={handleinput}
            />
            {prosum && (
              <span className="text-danger">Profile Summary is mandatory</span>
            )}
          </div>
        </div>
        <br />
        <span>
          <h5>Experiences</h5>
        </span>
        <br />
        <button
          className="btn btn-primary"
          onClick={addExperience}
          type="button"
        >
          <h5>Add Experience</h5>
        </button>
        {data.experiences.map((item, index) => {
          return (
            <>
              <div className="row" key={index}>
                <div className="col-md-4">
                  <label htmlFor={"designation" + item.id}>
                    <h5>Designation</h5>
                  </label>
                  <input
                    key={index}
                    type="text"
                    className="form-control"
                    id={"designation" + item.id}
                    name="designation"
                    onChange={(event) => {
                      handleexperience(index, event);
                    }}
                  />
                </div>
                <div className="col-md-4">
                  <label htmlFor={"role" + item.id}>
                    {" "}
                    <h5>Role</h5>
                  </label>
                  <input
                    key={index}
                    type="text"
                    className="form-control"
                    id={"role" + item.id}
                    name="role"
                    onChange={(event) => {
                      handleexperience(index, event);
                    }}
                  />
                </div>
                <div className="col-md-4">
                  <label htmlFor={"companyname" + item.id}>
                    {" "}
                    <h5>Company Name</h5>
                  </label>
                  <input
                    key={index}
                    type="text"
                    className="form-control"
                    id={"companyname" + item.id}
                    name="companyname"
                    onChange={(event) => {
                      handleexperience(index, event);
                    }}
                  />
                </div>
                <div className="col-md-4">
                  <label htmlFor={"durationfrom" + item.id}>
                    <h5>Duration From</h5>
                  </label>
                  <input
                    key={index}
                    type="date"
                    className="form-control"
                    id={"durationfrom" + item.id}
                    name="durationfrom"
                    onChange={(event) => {
                      handleexperience(index, event);
                    }}
                  />
                </div>
                <div className="col-md-4">
                  <label htmlFor={"durationto" + item.id}>
                    {" "}
                    <h5>Duration To</h5>
                  </label>
                  <input
                    key={index}
                    type="date"
                    className="form-control"
                    id={"durationto" + item.id}
                    name="durationto"
                    onChange={(event) => {
                      handleexperience(index, event);
                    }}
                  />
                </div>
              </div>
            </>
          );
        })}
        {expflag && (
          <span className="text text-danger">
            *Only past three experiences can be entered
          </span>
        )}
        <br />
        <h5>
          Technical Skills<h6>(one in each box)</h6>
        </h5>
        <br />
        <button type="button" className="btn btn-primary" onClick={addskills}>
          Add Skill
        </button>
        <div className="row">
          {data.skills.map((item, index) => {
            return (
              <div className="col-md-4" key={index}>
                <label htmlFor={"name" + item.id}>
                  {" "}
                  <h5>Skill Name</h5>
                </label>
                <input
                  type="text"
                  key={index}
                  id={"name" + item.id}
                  name="name"
                  className="form-control"
                  onChange={(event) => {
                    handleskills(index, event);
                  }}
                />
              </div>
            );
          })}
        </div>
        {skiflag && (
          <span className="text-danger">Maximum 10 skills can be added</span>
        )}
        <br />
        <span>
          <h5>
            Certificates <h6>(order of certificates from latest to oldest)</h6>
          </h5>
        </span>
        <br />
        <button className="btn btn-primary" type="button" onClick={addcerti}>
          Add Certificates
        </button>
        <br />
        {data.certificate.map((item, index) => {
          return (
            <div className="row" key={index}>
              <div className="col-md-6">
                <label htmlFor={"title" + item.id}>
                  <h5>Title/Name of the Certificates</h5>
                </label>
                <input
                  type="text"
                  className="form-control"
                  key={item.id}
                  id={"title" + item.id}
                  name="title"
                  onChange={(event) => {
                    handlecerti(index, event);
                  }}
                />
              </div>
              <div className="col-md-6">
                <label htmlFor={"link" + item.id}>
                  <h5>Link to Certificates</h5>
                </label>
                <input
                  type="text"
                  className="form-control"
                  key={item.id}
                  id={"link" + item.id}
                  name="link"
                  onChange={(event) => {
                    handlecerti(index, event);
                  }}
                />
              </div>
              <div className="col-md-6"></div>
            </div>
          );
        })}
        <br />
        {certiflag && (
          <span className="text-danger">
            Maximum 7 certificates can be added
          </span>
        )}
        <br />
        <span>
          <h5>Personal Profiles Link</h5>
        </span>
        <br />
        <button className="btn btn-primary" type="button" onClick={addperson}>
          Add new profile link
        </button>
        <br />
        {data.personalLink.map((item, index) => {
          return (
            <div className="row" key={index}>
              <div className="col-md-6">
                <label htmlFor={"titleprofile" + item.id}>
                  <h5>Profile Title</h5>
                </label>
                <input
                  type="text"
                  className="form-control"
                  key={item.id}
                  id={"titleprofile" + item.id}
                  name="title"
                  onChange={(event) => {
                    handleprofile(index, event);
                  }}
                />
              </div>
              <div className="col-md-6">
                <label htmlFor={"linkprofile" + item.id}>
                  <h5>Profile Link</h5>
                </label>
                <input
                  type="text"
                  className="form-control"
                  key={item.id}
                  id={"linkprofile" + item.id}
                  name="link"
                  onChange={(event) => {
                    handleprofile(index, event);
                  }}
                />
              </div>
            </div>
          );
        })}
        <br />
        {persoflag && (
          <span className="text-danger">
            *Maximum 7 profile link can be added
          </span>
        )}
        <br />
        <br />
        <button
          type="submit"
          className="btn btn-primary"
          onClick={handlesubmit}
        >
          Submit
        </button>
      </form>
    </div>
  );
};
export default ResumeForm;
